@import url('https://fonts.cdnfonts.com/css/cera-round-pro');


.popup-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}



.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-in-out;
  opacity: 1;

  z-index: 9999;
}
//   cursor:default !important;
 
// }

.popup {
  background-color: #fff;
  width: 372.15px;
  height: 476px;
  padding: 20px;
  
  
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  cursor:default !important;
  h2{
      cursor:default !important;
  }
  p{
      cursor:default !important;
  }
  
}
.popup.open {
  opacity: 1;
  transform: scale(1);
}
.popup.closed {
  opacity: 0;
}
// Add any additional styles for the popup content
.popup-content {
  max-width: 25.625rem;
 
  cursor:default !important;
  
  .title-content{
    align-items: flex-start;
    box-sizing: border-box;
    color: rgb(44, 44, 44);
    display: flex;
    font-family: Cera Pro, sans-serif;
    line-height: 24px;
    margin-top: 0px;
    margin-bottom: 30px;
    max-width: 335px
    
    
    
    }
    h2{
      box-sizing: border-box;
      color: rgb(0, 0, 0);
      font-family: Cera Pro, sans-serif;
      font-size: 18px;
      font-weight: 700;
      line-height: 25px;
      margin-bottom: 0px;
      margin-left: 0px;
      margin-right: 0px;
      margin-top: 0px;
      padding-right: 18px;
      text-transform: none
    }
    
    .lego-icon{
      width: 57px;
      height: 50px;
      margin-right: 15px;
    }
  }
  
  .open-button{
    display: flex;
    align-items: center;
  }
 
.span-open-button{
  box-sizing: border-box;
  color: rgb(0, 0, 0);
  cursor: pointer;
  font-family: Cera Pro, sans-serif;

  font-size: 12px;

  font-weight: 500;
  line-height: 70px;
  text-transform: none;
  align-items: center;
  justify-content: center;
  }
  
  .open-button img{
    
    margin-top: 5px;
  }

.close-button {
  position: absolute;
  border-radius: 0px;
  height: 3.75rem;
  width: 3.75rem;
  top: 0px;
  right:5px;
  
  border: none;
  background-color: transparent;
  font-size: 18px;
  cursor: pointer;
  border-left: 1px solid rgb(224, 224, 224);
  
  .close-icon{
    width: 30px;
    height: 30px;
    
  }
   
}
.close-button:hover{
  background-color: transparent;
}
