.cart-item {
    display: flex;
    gap: 20px;
    padding: 30px 0;
    border-bottom: 1px solid #e0e0e0;
  
    .item-info {
      display: flex;
      flex-direction: column;
      width: 60%;
    }
  
    .item-image {
      width: 135.83px;
      height: auto;
    }
  
    .item-details {
      display: flex;
      flex-direction: row;
      gap: 10px;
      flex-grow: 1;
  
      .item-title {
        font-family: "Inter", sans-serif;
        font-weight: 500;
        font-size: 15px;
        color: #000000;
      }
  
      .item-actions {
        display: flex;
        justify-content: space-between;
        align-items: center;
  
        .item-price {
          font-family: "Inter", sans-serif;
          font-weight: 700;
          font-size: 15px;
          color: #2c2c2c;
        }
  
        .quantity-input {
          display: flex;
          border: 1px solid #e0e0e0;
          border-radius: 5px;
          height: 2.7rem;
          flex: 0 1 45%;
          width: auto;
          margin: 0px 1.25rem;
  
          .quantity-btn {
              position: relative;
              cursor: pointer;
              flex-grow: 1;
              height: 100%;
              min-width: 2.5rem;
              border: 1px solid rgb(224, 224, 224);
              display: flex;
              align-items: center;
              border-radius: 0px 5px 5px 0px;
              background-color: rgb(255, 255, 255);
              justify-content: center;
              touch-action: manipulation;
          }
  
          .quantity-display {
              background-image: none;
              width: 100%;
              height: 100%;
              border: none;
              border-radius: 0px;
              cursor: text;
              text-align: center;
              text-indent: 0px;
              text-rendering: auto;
              text-shadow: none;
              word-spacing: 0px;
              padding: 0px;
              writing-mode: horizontal-tb;
              appearance: none;
              color: rgb(0, 0, 0);
          }
        }
  
        .wishlist-btn-wrapper {
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 0px 15.67px 0px 0px;
          gap: 10px;          
          width: 136.67px;
          height: 30px;
          left: 573.2px;
          top: 73px;
  
          .wishlist-btn {
            width: 30px;
            height: 30px;
            flex: none;
            order: 0;            
            left: calc(50% - 30px/2);
            top: 0px;
            background: #F8F8F8;
            box-sizing: inherit;

            .Wishlist-icon{
                height: 1.875rem;
                width: 1.875rem;
                box-sizing: inherit;
            }
          }
  
          .wishlist-text {
            width: 81px;
            height: 18px;
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 11px;
            line-height: 18px;
            display: flex;
            align-items: center;
            color: #000000;
            flex: none;
            order: 1;
          }
        }
      }
    }
  }
  